    &__title {
        margin: 15% 0;
        
        text-align: center;
        font: $book-pre-high;

    }

    &__blocks {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        flex-wrap: wrap;
        margin-top: 5%;

    }

    &__block {
        display: flex;
        flex-direction: column;
        place-items: center;
        text-align: center;
        height: max-content;
        width: 30%;
        min-width: 300px;
        position: relative;
        &-img {    
            position: absolute;
            bottom: 75%;
            width: 140px;
            height: 140px;
            
                img {
                    width: 100%;
                    height: 100%;
                    
                }
        }
        
    }

    &__text {
        padding-top: $medium;
        background-color: #F2F2F2;
        
        h5 {
            font: $book-medium;
            color: $color-black;
            font-weight: bold;
           
        }

        p {
           font: $book-low;
           color: $color-black;
           padding-bottom: $small;
        }
    }

}

@media (max-width:768px) {
    .about {
     
        &__block{
            margin-bottom: 15%;

            &:last-child {
                margin: auto;
            }
        }
        
    }
}
@media (max-width:500px) {
    .about {
        
        &__block{
            padding-bottom: 20%;
                margin: auto;
            
        }
        
    }
}