.footer {
    background-color: $color-favorit;
    color: $color-white;
    padding: $medium 0;

    &__wrapp {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        flex-wrap: wrap;
        gap: 50px;
    }

    &__logo {
        padding-top: 1.5%;
    }

    &__series {

        h4 {
            width: fit-content;
            border-bottom: 3px solid transparent;
            cursor: pointer;
            transition: $transition;

            &:hover {
                border-bottom: 3px solid $color-white;
                transition: $transition;
            }
        }

    }

    &__info {

        &-item {
            padding:0 0 5% 0;
        }

        a {
            font: $book-low;
            color: $color-white;
            letter-spacing: 1px;
            border-bottom: 1px solid transparent;
            cursor: pointer;
            transition: $transition;

            &:hover {
                border-bottom: 1px solid $color-white;
                transition: $transition;
            }
        }
    }

    &__socials {
        display: flex;
        padding-top: 1.5%;
        &-item {
            width: 30px;
            height: 30px;
            
            &:first-child {
                margin-right: $medium;
            }
        }
    }

    .copywrited {
        text-align: center;
        padding: $medium 0 $small 0;
        font: $book-low;
        color: #645e5e;

        span:nth-child(2) {
            font-weight: bold;
            text-transform: uppercase;
        }
    }
}

@media (max-width:500px) {
    .footer {
        &__wrapp {
            flex-direction: column;
            align-items: end;
            justify-content: center;
            gap: 20px;
        }
    }
}