@media (max-width:1024px) {
    .title {
      font-size: calc(20px + 3vw);
    }
  }
  
  @media (max-width:769px) {
    .title {
      font-size: calc(12px + 2.5vw);
    } 
  }
  
  @media (max-width:500px) {
    .title { 
      font-size: calc(10px + 3vw);         
    }
  }