.header {
   position: relative;
    z-index: 1;
    padding-top: $medium;

    &__logo {
        svg:hover path {
 
                fill: $color-hover;
    
        }

    }

    &__line {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__search {

        label {
            position: relative;
        }

        input {
            width: 400px;
            height: 30px;
            padding-left: $medium;
            border-radius: $medium;
            border: none;
            outline: none;
            cursor: pointer;
        }

        svg {
            position: absolute;
            left: 10px;
            top: 0;
        }
        
    }
    &__menu {
        display: flex;
        justify-content: space-between;
        align-items: center;
        
        svg {
            cursor: pointer;

            &:hover path {
                fill: $color-hover;
                transition: $transition;
            }
        }

        &-burger {
            position: relative;
            top: 3px;
            width: 35px;
            height: 25px;
            cursor: pointer;
            margin-left: $medium;

            span:nth-child(1) {
                width: 25px;
                height: 2px;
                background-color: $color-white;
                position: absolute;
                right: 5px;
                top: 16px;
                transition: $transition;
            }

            span:nth-child(2) {
               
                position: absolute;
                background-color: $color-white;
                right: 5px;
                top: 8px;
                width: 25px;
                height: 2px;
                transition: $transition;
            }

            span:nth-child(3) {
               
                position: absolute;
                background-color: $color-white;
                right: 5px;
                top: 0;
                width: 25px;
                height: 2px;
                transition: $transition;
            }

            
        }
    }

   
    
}

@media (max-width:769px) {
    .header {
        &__logo {
            svg {
                width: 130px;
                height: 53px;
            }
        }
        input {
            width: 300px;
        }
    }
}
@media (max-width:576px) {
    .header {
        input {
            width: 250px;
        }
    }
}
@media (max-width:500px) {
  .header {
        label {
            display: none;
        }
    }
}