.favorites {
    position: relative;

    &__title {
        padding: 7% 0 2% 0;
    }

    .grid {
        display: grid;
        width: 100%;
        grid-template-columns: 60% auto auto;
        grid-template-rows: auto auto;
        grid-gap: $small;
        color: $color-white;

        &__item {
            position: relative;
        }

        .item-1 {
            grid-column: 1 / 2;
            grid-row: 1 / 3;

            &-title {
                h4 {
                    position: absolute;
                    top:0;
                    right: 10%;
                    font: $book-pre-high;
                   
                    z-index: 11;
                    margin: $small 0;
                }
    
                span {
                    position: absolute;
                    top: 20%;
                    right: 10%;
                    font: $book-low;
                    z-index: 11;
                }
            }
            
            img {
                width: 100%;
                height: 100%;
                position: relative;
            }

            

            svg {
                position: absolute;
                bottom: 10%;
                right: 10%;
                cursor: pointer;
            }
        }
        

    }

    &__descr {
        background-color: #F2F2F2;
        height: auto;
        margin-top: 13%;
        padding: $medium;

        span {
            font: $book-medium;
            font-weight: bold;
            text-transform: uppercase;
        }
        p {
            font: $book-medium;
        }
    }
}


@media (max-width:820px) {
    .favorites {
        .grid {
            grid-template-columns: auto auto auto auto;
            grid-template-rows: auto auto auto auto;

            .item-1 {
                grid-column: 1 / 5;
                grid-row: 1 / 2;
                
            }
        }
        &__descr {
            p {
                font: $book-low;
            }
        }
    }
    

}
@media (max-width:550px) {
    .favorites {
        .grid {
            grid-template-columns: auto ;
            grid-template-rows: auto auto auto auto;

            .item-1 {
                h4 {
                    font-size: calc(15px + 2vw);
                    
                }
                span {
                    font-size: calc(9px + 1vw);
                    top: 25%;
                }
            }
            .item-2 {
                grid-column: 1 / 5;
                grid-row: 2 / 3;
            }
            .item-3 {
                grid-column: 1 / 5;
                grid-row: 3 / 4;
            }
            .item-4 {
                grid-column: 1 / 5;
                grid-row: 4 / 5;
            }
            .item-5 {
                grid-column: 1 / 5;
                grid-row: 5 / 6;
            }
        }
        
    }
    

}