.slide {


    &__block {
        position: relative;

        h3 {
            position: absolute;
            bottom: -5%;
            left: 34%;
            width: 100%;
            
            font: $book-medium;
            color: transparent;
            -webkit-text-stroke: 1px $color-white;
            text-transform: uppercase;
        }
        
        &:hover h3{
            -webkit-text-stroke: 3px $color-hover;
        }
    }
}

@media (max-width:1024px) {
    .slide {
      h3 {font-size: calc(5px + 2vw);}
    }
  }
  
  @media (max-width:769px) {
    .slide {
        h3 {
            bottom: -8%;
            left: 27%;
        }
      }
  }

  @media (max-width:500px) {
        .slide {
            h3 {
                font-size: calc(5px + 1.5vw);
                bottom: -12%;
                left: 25%;
            }
          }
  }

  @media (max-width:430px) {
    .slide {
        h3 {
            bottom: -15%;
            left: 20%;
        }
      }
}
  
