.mailing {
   height: auto;

    &__bg {
        margin-top: 15%;
    }

    &__wrapp {
        background-image: url(../../img/mailing-bg.jpg);
        background-position: 100px;
        background-repeat: no-repeat;
        background-size: contain;
        display: flex;
        flex-direction: column;
        place-items: center;
        padding: 100px;

        form {
            display: flex;
            flex-direction: column;
            place-items: center;

            label {
                margin-bottom: 10%; 
                input {
                     width: 400px;
                     height: 40px;
                     border: 3px solid $color-black;
                     padding: 0 $medium ;
                     font: $book-low;
                 }
            }
            button {
                width: 250px;
                height: 45px;
                background-color: $color-black;
                color: $color-white;
                text-transform: uppercase;
                font: $book-low;
                font-weight: bold;
                &:hover {
                    background-color: $color-white;
                    color: $color-black;
                }
            }
        }
        
    }

    &__title {
            
            text-align: center;
        h3 {
            width: 100%;
            font: $book-pre-high;
            
        }
    }
}

@media (max-width:992px) {
    .mailing {
        &__title {
            h3 {
                font-size: calc(10px + 2.5vw);
            }
        }
    }
}
@media (max-width:850px) {
    .mailing {
        &__title {
            h3 {
                font-size: calc(12px + 3vw);
            }
        }

        &__wrapp {
            form {
                label{
                    input {
                    width: 300px;
                    height: 40px;
  
                    }
                }
                button {
                    width: 200px;
                    height: 40px;
                }
            }
        }
    }
}