
.popup-fade {
    position: absolute;
    top: 5%;
    display: none;
    
}
.popup-fade:before {
	content: '';
	background: #000;
	position: fixed; 
	left: 0;
	top: 0;
	width: 100%; 
	height: 100%;
	opacity: 0.7;
	z-index: 9999;
    
}
.popup {
	position: fixed;
	top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
	padding: $medium;
	width: 346px;
    height: 550px;
    background-color: $color-white;
	z-index: 99999;
	opacity: 1;	

    &__head {
        display: flex;
        justify-content: space-between;
        align-items: center;

        &-lang {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 83px;
            font: $book-medium;
            font-weight: bold;
            a {
                transition: $transition;
    
                &:hover {
                    color: $color-hover;
                    transition: $transition;
                }
            }
            div{
                width: 30px;
                transform: rotate(30deg);
            }
    
            
        }
    }

    &__body {
        margin-top: 20%;
        display: flex;
        flex-direction: column;
        justify-content: flex-start; 
        font: $book-medium;
        
        h4 {
            width: fit-content;
            margin: 10% 0 0 0;
            border-bottom: 3px solid transparent;
            cursor: pointer;
            transition: $transition;

            &:hover {
                border-bottom: 3px solid $color-black;
                transition: $transition;
            }
        }
    }

    &__menu {
        margin-top: 10%;

        &-item {
            margin-top: 5%;

            a {
                font: $book-low;
                letter-spacing: 1px;
                border-bottom: 1px solid transparent;
                cursor: pointer;
                transition: $transition;
    
                &:hover {
                    border-bottom: 1px solid $color-black;
                    transition: $transition;
                }
            }
        }

        &--socials {
            margin-top: 15%;

            img {
                width: 30px;
                height:30px;

                &:first-child {
                    margin-right: 5%;
                }
            }
        }
    }
}
