.news {
    position: relative;

    &__title {
        text-align: center;
    }

    &__blocks {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-top: 10%;
    }

    &__block {
        width: 30%;
        min-width: 280px;
        
        
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        text-align: center;
        background-color: #F2F2F2;

        &-img {
            width: 100%;
            

            img {
                width: 100%;
                height: 100%;
            }
        }

        h4 {
            font: $book-medium;
            font-weight: bold;
            height: 30%;
            padding: 0 $small;
        }

        p {
           font: $book-low; 
           margin-bottom: 10%;
           padding: 0 $small;
        }
        button {
            padding: $small;
            text-transform: uppercase;
            color: $color-black;
            background-color: transparent;
            border: 3px solid $color-black;
            font: $book-low;
            font-weight: bold;
        }    

        &:hover button {
            color: $color-white;
            background-color: $color-black;
        }
    }

    &__block-img {
        width: 100%;
        height: 50%;

        img {
            width: 100%;
            height: 100%;
        }
    }
}

@media (max-width:768px) {
    .news {
     
        &__block{
            margin-bottom: 15%;
            min-width: 300px;
            &:last-child {
                margin: auto;
            }
        }
        
    }
}

@media (max-width:500px) {
    .news {
        
        &__block{
            margin: auto;
            margin-bottom: 10%;
        }
        
    }
}