.hero {
        height: auto;
        margin-top: -35%;
        padding-top: 35%;
        background-image: url(../../img/hero-bg.jpg);
        background-position: center;
        background-repeat: no-repeat;
        
        background-size:  cover;
        display: flex;
        flex-direction: column;
        place-items: center;


    // &__bg {
    //     background-image: url(../../img/hero-bg.jpg);
    //     background-position: center;
    //     background-size: cover;
    //     background-repeat: no-repeat;
    //     filter: brightness(40%);
    //     height: 1200px;
    //     min-height: 1000px;
    //     position: absolute;
    //     left: 0;
    //     top: 0;
    //     right: 0;
    //     bottom: 0;
    //     z-index: -1;
    // }
    
        

    &__wrapp {
        margin-top: 25%;
        margin-bottom: 35%;
    }

    &__sup-title {
       
        h2 {
            font: $book-medium;
            color: $color-white;
        }
        
    }

    &__title {
        h1 {
            margin: 0;
            font:$book-high;
            color: $color-white;
        }
    }

    &__btn {
        margin-top: 5%;
        button {
            width: 206px;
            height: 41px;
            color: $color-white;
            background-color: $color-black;
            text-transform: uppercase;
            border: none;
            font: $book-low;
            letter-spacing: 1px;
            transition: $transition;
            cursor: pointer;
            
            &:hover {
                background-color: $color-white;
                color: $color-black;
                transition: $transition;
            }
        }
        
    }
}

@media (max-width:1024px) {
    .hero {
            h1 {
                font-size: calc(20px + 3vw);
            }
            h2 {
                font-size: calc(5px + 1.5vw);
            }
            
    }
}
@media (max-width:769px) {
    .hero {
            h1 {
                font-size: calc(12px + 2.5vw);
            }
            h2 {
                font-size: calc(5px + 1.2vw);
            }
            button {
                font-size: calc(5px + 1.5vw);
                width: 186px;
                height: 36px;
            }
    }
}
@media (max-width:500px) {
    .hero {
            h1 {
                font-size: calc(10px + 3vw);
            }
            h2 {
                font-size: calc(2.5px + 2vw);
            }
            button {
                font-size: calc(4px + 1.5vw);
                width: 176px;
                height: 32px;
            }
    }
}